import { createPopper } from '@popperjs/core';

export default class Popovers {
    constructor() {
        this.slider = document.querySelector("#sliderDetail");
        this.events();
    }

    events() {
        let self = this;
        if(this.slider) {
            this.slider.addEventListener('click', function(e) {
                const button = $(e.target).closest('[data-card-id]');
                if(button.length > 0) {
                    document.querySelector("[data-card]").setAttribute('data-card', button[0].getAttribute('data-card-id'));
                    const tooltip = document.querySelector("[data-card='" + button[0].getAttribute('data-card-id') + "']");
                    const popInstance = self.init(button[0], tooltip);

                    requestAnimationFrame(function() {
                        document.addEventListener("click", function (e) {
                            self.destroy(button[0], tooltip, popInstance);
                        },{once: true});
                    })
                }
            })
        }
    }

    init(button, tooltip) {
        button.classList.add("active");
        tooltip.classList.add("active");
        return createPopper(button, tooltip, {
            placement: "right",
            modifiers: [
                {
                    name: "preventOverflow",
                    options: {
                        altAxis: false,
                        boundary: tooltip.parentElement,
                        padding: 8,
                    },
                },
                {
                    name: "offset",
                    options: {
                        offset: [18, -21],
                    },
                },
            ],
        });
    }

    destroy(button, tooltip, popInstance) {
        button.classList.remove("active");
        tooltip.classList.remove("active");
        popInstance.destroy();
    }
}